
[currency="rub"] .symbol-before:before,
[currency="rub"] .symbol-after:before {
  content: '₽';
}
[currency="usd"] .symbol-before:before,
[currency="usd"] .symbol-after:before {
  content: '$';
}
[currency="eur"] .symbol-before:before,
[currency="eur"] .symbol-after:before {
  content: '€';
}
[currency="cny"] .symbol-before:before,
[currency="cny"] .symbol-after:before {
  content: '¥';
}
.price-per-hour {
  font-size: 14px;
  line-height: 24px;
  color: #c0c0c0;
}
.price-per-hour.active .symbol-before,
.price-per-hour.active .symbol-after {
  color: #333;
}
.price-per-hour .symbol-before {
  display: none;
}
.price-per-hour .symbol-after {
  display: inline-block;
}
.price-per-hour .label:first-child {
  margin-right: 0.5em;
}
.price-per-hour .label:last-child {
  margin-left: 0.5em;
}
.price-per-hour input {
  border: 0;
  padding: 0 3px;
  background-color: transparent;
  font-size: 14px;
  height: 20px;
  line-height: 20px;
  display: inline-block;
  outline: none;
  width: 50px;
  text-align: right;
  font-weight: 500;
}
.price-per-hour input::-webkit-input-placeholder {
  color: #cdcdcd;
}
.price-per-hour input:-ms-input-placeholder {
  color: #cdcdcd;
}
.price-per-hour input::placeholder {
  color: #cdcdcd;
}
[is-currency-symbol-before="true"] .price-per-hour .symbol-before {
  display: inline-block;
}
[is-currency-symbol-before="true"] .price-per-hour .symbol-after {
  display: none;
}
[is-currency-symbol-before="true"] .price-per-hour input {
  text-align: left;
}


