
.group-item > .item {
  margin: 6px auto;
  padding-top: 4px;
  padding-bottom: 4px;
  font-size: 18px;
  line-height: 24px;
}
.group-item .group-item {
  margin-left: 20px;
}
.group-item .group-item .group-item > .item {
  margin: 5px auto;
  font-size: 14px;
  line-height: 20px;
}
.group-item .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
}
.group-item .name {
  margin-right: 0.5em;
  width: 100%;
  cursor: pointer;
}
.group-item .cost {
  font-size: 80%;
  color: #333;
  margin-left: 0.375em;
  position: relative;
  padding-left: 1em;
  display: inline-block;
  white-space: nowrap;
}
.group-item .cost:after {
  content: '=';
  margin-right: 0.375;
  position: absolute;
  display: block;
  color: #c0c0c0;
  left: 0px;
  top: 0px;
}
.group-item .cost:before {
  float: right;
  margin-left: 0.25em;
}
.group-item .duration {
  font-size: 80%;
  color: #c0c0c0;
  display: inline-block;
  cursor: pointer;
}
@media (min-width: 768px) {
.group-item .name {
    width: auto;
}
.group-item .duration,
  .group-item .cost {
    font-size: 100%;
    line-height: 1;
}
}
.group-item .item.edit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  padding-top: 0px;
  padding-bottom: 0px;
}
.group-item .item.edit .name {
  width: calc(100% + 8px);
  margin: 0 0 0.5em -8px;
}
.group-item .item.edit .name.non-editable {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 8px;
  box-sizing: box-sizing;
}
.group-item .item.edit .name textarea {
  width: 100%;
  font-size: inherit;
  border: none;
  padding: 4px 8px;
  margin: 0px;
  position: relative;
  box-sizing: border-box;
  resize: none;
  border-radius: 5px;
  line-height: inherit;
  display: block;
  background-color: #fff;
  color: #333;
}
.group-item .item.edit .name textarea:focus {
  background-color: #303030;
  color: #f5f5f5;
}
.group-item .item.edit .duration {
  width: 6.5em;
  margin: 0 0 0 -8px;
  line-height: inherit;
  font-size: inherit;
}
.group-item .item.edit .duration textarea,
.group-item .item.edit .duration input {
  text-align: left;
  display: block;
  width: 100%;
  font-size: inherit;
  font-family: PT Mono, monospace;
  box-sizing: border-box;
  padding: 4px 8px;
  margin: 0;
  border: none;
  line-height: inherit;
  border-radius: 5px;
  resize: none;
  background-color: #fff;
  color: #333;
}
.group-item .item.edit .duration textarea:focus,
.group-item .item.edit .duration input:focus {
  background-color: #303030;
  color: #f5f5f5;
}
.group-item .item.edit .actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 4px;
  padding: 4px;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: 5.75em;
  position: absolute;
  right: 0px;
  bottom: 0px;
}
.group-item .item.edit .icon-button {
  font-size: 140%;
}
.group-item .item.edit .icon-button + .icon-button {
  margin-left: 0.375em;
}
@media (min-width: 768px) {
.group-item .item.edit {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.group-item .item.edit .name {
    width: calc(100% - 12.75em);
    margin: 0 0 0 -8px;
}
.group-item .item.edit .duration {
    margin-left: 0.5em;
}
.group-item .item.edit .duration input {
    text-align: center;
}
.group-item .item.edit .actions {
    margin-left: auto;
    position: static;
}
}
.view > .group-item > .item {
  font-size: 32px;
  line-height: 42px;
  font-weight: 400;
}
.view > .group-item > .item .duration,
.view > .group-item > .item .cost {
  font-size: 80%;
}
.view > .group-item > .item.edit .name textarea {
  font-weight: 400;
}
@media (min-width: 768px) {
.view > .group-item > .item.edit .actions {
    width: 5.5em;
}
.view > .group-item > .item.edit .name {
    width: calc(100% - 11.5em);
}
.view > .group-item > .item.edit .name textarea {
    font-weight: 400;
}
}
.view > .group-item:not(:first-child) > .item {
  margin-top: 40px;
}
[currency="rub"] .cost:before {
  content: '₽';
}
[currency="usd"] .cost:before {
  content: '$';
}
[currency="eur"] .cost:before {
  content: '€';
}
[currency="cny"] .cost:before {
  content: '¥';
}
[is-currency-symbol-before="true"] .cost:before {
  float: none;
  margin-right: 0.25em;
  margin-left: 0em;
}


