
.edit-task {
  min-height: 20px;
  font-size: 14px;
  line-height: 20px;
  background-color: #f8f8ff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: -10px;
  font-weight: 400;
  width: 100%;
}
.edit-task textarea {
  margin: 0;
  padding: 0px 10px;
  font-size: inherit;
  line-height: inherit;
  background-color: #f8f8ff;
  color: #333;
  border: 0px;
  resize: none;
  outline: none;
  border-radius: 5px;
}


