
.modal-wrapper {
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
}
.modal-wrapper:not([inline="true"]) {
  position: fixed;
  overflow: auto;
  background-color: rgba(48,48,48,0.1);
}
.modal-wrapper:not([inline="true"]) .modal-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  background-color: #f8f8ff;
  box-shadow: 0px 0px 20px rgba(51,51,51,0.1);
  border-radius: 5px;
  padding: 20px 0px;
}
.modal-wrapper:not([inline="true"]) .modal-header,
.modal-wrapper:not([inline="true"]) .modal-body,
.modal-wrapper:not([inline="true"]) .modal-footer {
  padding: 10px 40px;
}
.modal-wrapper:not([inline="true"]) .modal-header > :first-child,
.modal-wrapper:not([inline="true"]) .modal-body > :first-child,
.modal-wrapper:not([inline="true"]) .modal-footer > :first-child {
  margin-top: 0px;
}
.modal-wrapper:not([inline="true"]) .modal-header > :last-child,
.modal-wrapper:not([inline="true"]) .modal-body > :last-child,
.modal-wrapper:not([inline="true"]) .modal-footer > :last-child {
  margin-bottom: 0px;
}
.modal-wrapper[inline="true"] {
  position: absolute;
}
.modal-wrapper[inline="true"] .modal-container {
  position: absolute;
  right: 0px;
}


