
.petrov {
  padding: 20px;
  margin: 20px;
}
.petrov .method {
  margin-right: 20px;
}
.petrov .method:after {
  content: "/";
}
.petrov .url {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.petrov input[type="text"] {
  border: 0;
  padding: 0;
  background-color: transparent;
  font-weight: 500;
  outline: none;
}
.petrov input[type="text"]::-webkit-input-placeholder {
  color: #c0c0c0;
}
.petrov input[type="text"]:-ms-input-placeholder {
  color: #c0c0c0;
}
.petrov input[type="text"]::placeholder {
  color: #c0c0c0;
}
.petrov input[type="text"].error {
  text-align: center;
  box-shadow: 0px 1px 0px 0px #f15a22;
}
.petrov .send {
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.petrov .send button + button {
  margin-left: 10px;
}
.petrov button {
  border: none;
  border-radius: 3px;
  border-bottom: solid 3px #dcdcdc;
  background-color: #ececec;
  height: 40px;
  padding: 0 15px;
  min-width: 70px;
  cursor: pointer;
  outline: none;
  vertical-align: top;
}
.petrov button[disabled] {
  opacity: 0.5;
  cursor: default;
  pointer-events: none;
}
.petrov button:hover,
.petrov button.active {
  background-color: #f15a22;
  color: #fff;
  border-color: #f15a22;
}
.petrov button:active {
  background-color: #f15a22;
  color: #fff;
  border-bottom: none;
  border-top: solid 3px #f15a22;
}
.petrov .response {
  overflow-x: hidden;
  margin: 40px 0px;
}
.petrov .response.error {
  color: #f15a22;
}
.petrov .response pre {
  margin: 0;
}


