
body,
input,
textarea,
button,
select,
option {
  font-family: Rubik, sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  color: #333;
  outline: none;
}
select,
option {
  font-size: 14px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  margin: 20px auto;
}
strong {
  font-weight: 500;
}
p {
  margin: 20px auto;
  font-weight: 300;
}
h4 {
  font-size: 16px;
  line-height: 24px;
}
h2 {
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 15px;
}
.material-icons {
  font-size: 18px;
}
.material-icons.md-14 {
  font-size: 14px;
}
.material-icons.md-16 {
  font-size: 16px;
}
.material-icons.md-18 {
  font-size: 18px;
}
.material-icons.md-20 {
  font-size: 20px;
}
.material-icons.md-24 {
  font-size: 24px;
}
* + .material-icons,
.material-icons + * {
  margin-left: 5px;
}
[valign="middle"] {
  vertical-align: middle;
}
a {
  cursor: pointer;
}
a[href] {
  color: #4682b4;
}
[color] {
  position: relative;
}
[color]:before {
  content: ' ';
  position: absolute;
  left: -1em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0.5em;
  height: 0.5em;
  border-radius: 30%;
  background-color: #dcdcdc;
}
[color="0"]:before {
  background-color: #72cff9;
}
[color="1"]:before {
  background-color: #7d89cd;
}
[color="2"]:before {
  background-color: #71c5bd;
}
[color="3"]:before {
  background-color: #bedd9a;
}
[color="4"]:before {
  background-color: #e3ec91;
}
[color="5"]:before {
  background-color: #f5e975;
}
[color="6"]:before {
  background-color: #ffc571;
}
[color="7"]:before {
  background-color: #ff8d69;
}
[color="8"]:before {
  background-color: #f381a8;
}
[color="9"]:before {
  background-color: #c886d3;
}
[color="10"]:before {
  background-color: #b4a099;
}
[color="11"]:before {
  background-color: #c0cbd1;
}
button,
.button {
  border: 0px;
  background-color: #ececec;
  border-radius: 5px;
  line-height: 20px;
  font-size: 18px;
  font-weight: 300;
  padding: 10px 40px;
  cursor: pointer;
  border-bottom: solid #dcdcdc 3px;
  outline: none;
}
button:active,
.button:active {
  border-top: solid #d5d5d5 3px;
  border-bottom-width: 0px;
  background-color: #e5e5e5;
}
.icon-button {
  display: inline-block;
}
.icon-button i.material-icons {
  font-size: inherit;
  line-height: inherit;
  display: block;
}
.icon-button.lg {
  font-size: 24px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
}
.long-click {
  position: fixed;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  left: 50%;
  top: 50%;
  width: 50px;
  height: 50px;
  pointer-events: none;
}
.long-click .mask {
  width: 50%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 0px;
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation: mask-swap steps(2, end) 0.375s;
          animation: mask-swap steps(2, end) 0.375s;
}
.long-click .spinner {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  overflow: hidden;
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation: pie-rotation 0.375s linear;
          animation: pie-rotation 0.375s linear;
}
.long-click .spinner:after {
  content: ' ';
  position: absolute;
  left: -100%;
  top: 0px;
  width: 200%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 50%;
  border: solid rgba(51,51,51,0.1) 10px;
}
.long-click .spinner-clone {
  width: 50%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0px;
  overflow: hidden;
  -webkit-animation: pie-swap steps(1, end) 0.375s;
          animation: pie-swap steps(1, end) 0.375s;
  opacity: 1;
}
.long-click .spinner-clone:after {
  content: ' ';
  position: absolute;
  left: -100%;
  top: 0px;
  width: 200%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 50%;
  border: solid rgba(51,51,51,0.1) 10px;
}
@-webkit-keyframes pie-rotation {
0% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
49.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
99.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes pie-rotation {
0% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
49.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
99.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@-webkit-keyframes pie-swap {
0% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@keyframes pie-swap {
0% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes mask-swap {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes mask-swap {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
body {
  margin: 0;
  padding: 0;
  background-color: #f8f8ff;
  position: relative;
}
.app > .scrollable {
  height: 100vh;
  overflow: auto;
}
.page-wrapper {
  position: relative;
}
.page {
  background-color: #f8f8ff;
  padding-top: 60px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  opacity: 1;
  transition: all 0.3s ease-out;
}
.page.modal-active {
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
  opacity: 0.25;
}
@media (min-width: 480px) {
.page {
    padding-left: 30px;
    padding-right: 30px;
}
}
@media (min-width: 768px) {
.page {
    padding-top: 110px;
    padding-left: 60px;
    padding-right: 60px;
}
}
@media (min-width: 1366px) {
.page {
    padding-left: 140px;
    padding-right: 140px;
}
}
.view-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: solid 1px rgba(220,220,220,0.75);
}
.view-menu .price-per-hour {
  margin-bottom: 20px;
  display: none;
}
.view-menu .view-switch {
  line-height: 24px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  text-align: right;
}
.view-menu .view-switch a {
  padding: 0 3px;
  margin-left: 5px;
  cursor: pointer;
  font-size: 13px;
  display: inline-block;
  position: relative;
}
.view-menu .view-switch a:after {
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 3px;
  display: block;
  width: 100%;
  background-color: #dcdcdc;
}
.view-menu .view-switch a:hover:after {
  content: ' ';
}
.view-menu .view-switch a.active:after {
  content: ' ';
  background-color: #333;
}
@media (min-width: 768px) {
.view-menu {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.view-menu .price-per-hour {
    display: block;
    margin-bottom: 0px;
}
.view-menu .view-switch {
    margin-left: auto;
}
}
section.tasks,
section.months,
section.storage,
section.days,
section.years {
  margin: 20px auto 20px auto;
}
main {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
.timer {
  margin-bottom: 60px;
}
@media (min-width: 768px) {
.timer {
    margin-bottom: 90px;
}
}


