
.timer {
  position: relative;
}
.timer button {
  height: 60px;
  box-sizing: border-box;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  background-color: transparent;
  width: 100%;
  text-align: center;
  outline: none;
  border-bottom: solid 4px #dcdcdc;
  background-color: #ececec;
  position: relative;
}
.timer button:active {
  border-bottom-width: 0px;
  border-top: solid 4px #cf420d;
  background-color: #f15a22;
  color: #fff;
}
.timer button .main,
.timer button .sec,
.timer button .ms {
  font-family: PT Mono, monospace;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
}
.timer button .main {
  font-size: 30px;
  line-height: 30px;
  margin-left: -15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.timer button .delimiter {
  font-size: 26px;
}
.timer button .sec {
  font-size: 14px;
  line-height: 15px;
  width: 30px;
  text-align: center;
  display: block;
  margin-top: -6px;
  margin-left: 50px;
}
.timer button .ms {
  font-size: 10px;
  line-height: 10px;
  width: 30px;
  margin-top: 6px;
  margin-left: 50px;
  text-align: center;
  display: block;
}
.timer button .hrs.low,
.timer button .delimiter.low,
.timer button .min.low {
  opacity: 0.5;
}
.timer textarea {
  margin-top: 20px;
  height: 60px;
  border-radius: 10px;
  display: block;
  outline: none;
  width: 100%;
  box-sizing: border-box;
  padding: 14px 20px 14px 20px;
  text-align: center;
  resize: none;
  background-color: #fff;
  border: solid 1px #dcdcdc;
  font-size: 24px;
  line-height: 30px;
}
.timer textarea::-webkit-input-placeholder {
  color: #cdcdcd;
}
.timer textarea:-ms-input-placeholder {
  color: #cdcdcd;
}
.timer textarea::placeholder {
  color: #cdcdcd;
}
.timer.active button {
  border-bottom-width: 0px;
  border-top: solid 4px #cf420d;
  background-color: #f15a22;
  color: #fff;
}
.timer.active textarea {
  background-color: #454545;
  color: #f5f5f5;
  border-color: #303030;
  border-bottom-width: 1px;
}
@media (min-width: 768px) {
.timer button {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 200px;
    bottom: 0px;
    height: auto;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}
.timer textarea {
    padding-left: 220px;
    text-align: left;
    margin-top: 0px;
}
}


