
.storage-item {
  margin: 6px auto 20px auto;
}
@media (min-width: 768px) {
.storage-item {
    margin-bottom: 6px;
}
}
.storage-item .read {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding-top: 4px;
  padding-bottom: 4px;
}
@media (min-width: 768px) {
.storage-item .read {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
}
}
.storage-item .start,
.storage-item .details,
.storage-item .duration {
  display: block;
  cursor: pointer;
}
.storage-item .start {
  color: #c0c0c0;
  font-size: 14px;
  font-family: PT Mono;
  white-space: nowrap;
  width: 165px;
}
@media (min-width: 768px) {
.storage-item .start {
    line-height: 1;
}
}
.storage-item .details {
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
.storage-item .details {
    width: auto;
    max-width: calc(100% - 320px);
    margin: 0px 10px 0px 23px;
}
}
.storage-item .duration {
  color: #c0c0c0;
  font-family: PT Mono;
  font-size: 14px;
  width: 100px;
}
.storage-item .duration:before {
  content: "=";
  margin-right: 10px;
}
@media (min-width: 768px) {
.storage-item .duration {
    line-height: 1;
}
}
.storage-item .edit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  width: 100%;
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
}
.storage-item .edit .start {
  width: 180px;
  margin-left: -8px;
  line-height: inherit;
  margin-bottom: 0.5em;
}
.storage-item .edit .start input {
  text-align: left;
  display: block;
  width: 100%;
  font-size: inherit;
  font-family: inherit;
  box-sizing: border-box;
  padding: 4px 8px;
  margin: 0;
  border: none;
  line-height: inherit;
  border-radius: 5px;
  resize: none;
  background-color: #fff;
  color: #333;
}
.storage-item .edit .start input:focus {
  background-color: #303030;
  color: #f5f5f5;
}
.storage-item .edit .details {
  width: calc(100% + 8px);
  margin: 0 0 0.5em -8px;
}
.storage-item .edit .details textarea {
  width: 100%;
  font-size: inherit;
  border: none;
  padding: 4px 8px;
  margin: 0px;
  position: relative;
  box-sizing: border-box;
  resize: none;
  border-radius: 5px;
  line-height: inherit;
  display: block;
  background-color: #fff;
  color: #333;
}
.storage-item .edit .details textarea:focus {
  background-color: #303030;
  color: #f5f5f5;
}
.storage-item .edit .duration {
  width: 6.5em;
  margin-right: 0px;
  margin-left: -8px;
  line-height: inherit;
}
.storage-item .edit .duration:before {
  content: none;
}
.storage-item .edit .duration input {
  text-align: left;
  display: block;
  width: 100%;
  font-size: inherit;
  box-sizing: border-box;
  padding: 4px 8px;
  border: none;
  line-height: inherit;
  border-radius: 5px;
  resize: none;
  background-color: #fff;
  color: #333;
  font-family: inherit;
}
.storage-item .edit .duration input:focus {
  background-color: #303030;
  color: #f5f5f5;
}
.storage-item .edit .actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: 5.75em;
  position: absolute;
  right: 0px;
  bottom: 0px;
  padding-top: 4px;
  padding-bottom: 4px;
}
.storage-item .edit .icon-button {
  font-size: 140%;
}
.storage-item .edit .icon-button + .icon-button {
  margin-left: 0.375em;
}
@media (min-width: 768px) {
.storage-item .edit {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.storage-item .edit .start {
    margin-bottom: 0;
}
.storage-item .edit .details {
    margin-bottom: 0;
    margin-left: 0.5em;
    width: calc(100% - 12.25em - 180px);
}
.storage-item .edit .duration {
    margin-left: 0.5em;
}
.storage-item .edit .duration input {
    text-align: center;
}
.storage-item .edit .actions {
    margin-left: auto;
    position: static;
}
}
.timer-active .storage-item:first-child {
  color: #f15a22;
}
.timer-active .storage-item:first-child .start,
.timer-active .storage-item:first-child .duration {
  font-weight: 700;
  color: #f15a22;
}
.timer-active .storage-item:first-child .details {
  font-weight: 500;
}
.timer-active .storage-item:first-child .start input,
.timer-active .storage-item:first-child .details textarea,
.timer-active .storage-item:first-child .duration input {
  color: #f15a22;
}
.timer-active .storage-item:first-child .start input:focus,
.timer-active .storage-item:first-child .details textarea:focus,
.timer-active .storage-item:first-child .duration input:focus {
  color: #f26b38;
}
.timer-active .storage-item:first-child .start input,
.timer-active .storage-item:first-child .duration input {
  font-weight: bold;
}
.timer-active .storage-item:first-child .details textarea {
  font-weight: 500;
}


